import {atom} from "recoil";

export const todoData = atom({
    key: 'TODO_DATA', // unique ID (with respect to other atoms/selectors)
    default: [
        {
            title: "일반",
            showContent:true,
            color: "black",
            contents:[
                {
                    description: "투두 메이트 리뷰",
                    done: true,
                },
            ]
        },
    ],
});