import styled from "styled-components";
import produce from "immer";
import {useCallback, useEffect, useRef, useState} from "react";
import {useRecoilState} from "recoil";
import {todoData} from "../../atoms";
// 스타일 태그 제작소
const ToDoContainer = styled.div`
  position: relative;
  width: 320px;
  height: 640px;
  border: 2px solid;
  box-sizing: border-box;
  // 자식 요소 가운데 정렬
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0, ${props=> props.bright});
  transition: all 1s;
`;
const InnerContainer = styled.div`
  // ToDoCancel 의 부모 요소
  
  width: 300px;
  height: 620px;
`;
const ToDoTitle = styled.header`
  font-size: 14px;
  font-weight: bolder;
  text-align: center;
  // 
  
  // ToDoDescription 과의 공간
  margin-bottom: 30px;
`;
const ToDoCancel = styled.button`
  font-size: 30px;
  border: none;
  background-color: inherit;
  position: absolute;
  top:0px;
  left:275px;
  transform: rotate(-45deg);
`;
const ToDoDescription = styled.p`
  font-size: 12px;
  font-weight: bold;
  letter-spacing: -1px;
  // ToDoCategory 와의 공간
  margin-bottom: 14px;
`;
const ToDoCategoryContainer = styled.div`
    height: 500px;
`;
const ToDoCategory = styled.div`
  
`;
const ToDoCategoryButton = styled.button`
  background-color: #f4f4f4;
  font-size: 11px;
  font-weight: bold;
  height: 25px;
  border: none;
  border-radius: 5px;
`
const ToDoCategoryButtonName = styled.span`
  margin-right: 8px ;
`;
const ToDoPlus = styled.span`
    height: 11px;
    border-radius: 6.5px;
    background-color: #ffffff;
`
const ToDoThing = styled.div`
    height: 25px;
    
`;
const ToDoModalCancel= styled.button`
  cursor:pointer;
  font-size: 30px;
  border: none;
  border-radius: 50%;
  position:relative;
  top: -30px;
  left: 60px;
  background-color: inherit;
  transform: rotate(-45deg);
`

const ToDoModal = styled.div`
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  // 모달의 크기
  width: 150px;
  height: 100px;
  background: white;
  border-radius: 10px;
  // 모달의 존재
  display: ${props => {
    return props.exist   
  }};
  align-items: center;
`
const ToDoModalInput = styled.input`
  width: 100px;
  height:20px;
  border-radius: 10px;
  box-sizing: border-box;
`;
const ToDoModalButton = styled.button`
    cursor:pointer;
    width: 50px;
    height: 20px;
    border: none;
   border-radius: 25px;
  box-sizing: border-box;
`;
const ToDoThingCheckBox = styled.input.attrs(() => ({
    type: "checkbox",
}))`
    transform: scale(1.5);
    accent-color: #000000;
    border-style: none
`;
const ToDoThingName = styled.span`
  font-size: 12px;
  font-weight: bold;
`
const ToDoCategoryInputContainer = styled.div`
  display:flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  box-sizing: border-box;
`


// 하드코딩 데이터
const todo_data = [
    {
        title: "기본",
        showContent:false,
        color: "black",
        contents:[]
    },
]


const ToDo = () => {
    const [categoryInput, setCategoryInput] = useState("");
    const [todoInput, setTodoInput] = useState("");
    const [data, setData] = useState(todo_data);
    const [currentCategory, setCurrentCategory] = useState(0);
    const [bright, setBright] = useState(0);
    const [exist, setExist] = useState("none");


    // 모달 토글 기능
    const toggleModal = () => {
        bright === 0 ?
            (()=>{
                setBright(0.4)
                setTimeout(() => {
                    setExist("flex")
                },250)
            })() :
            (()=>{
                setExist("none")
                setTimeout(() => {
                    setBright(0);
                },250)
            })()
    }

    // 모달 열기
    const openModal = (event,index) => {
        event.stopPropagation();
        setCurrentCategory(index);
        // 밝기 토글하기
        toggleModal();
    }

    // 카테고리 색깔 변경하기
    // 카테고리인풋
    const changeCategoryInput = (e) => {
           setCategoryInput(e.currentTarget.value);
    }
    // 투두 인풋
    const changeTodoInput = (e) => {
        setTodoInput(e.currentTarget.value);
    }


    const addCategory = () => {
        const nextState= produce(data, draftState => {
            draftState.push({
                title: categoryInput,
                showContent:false,
                color: "black",
                contents:[]
            });
        })
        setData(nextState);
        setCategoryInput("");
    }
    // 카테고리 펼치기
    const toggleShowContent = useCallback((event,index) => {
        console.log("toggle_showContent")
        const nextState = produce(data, draftState => {
            draftState[index].showContent = !draftState[index].showContent;
        })
        setData(nextState);
    },[data]);
    // 투두 추가하기
    const addTodo = (event) => {
        const nextState = produce(data, draftState => {
            draftState[currentCategory].contents.push(
                {
                    description: todoInput,
                    done: false,
                }
            )
        });
        setData(nextState);
        setTodoInput("");
        toggleModal();
    }
    // 투두 체크하기
    const toggleDone = (event,index,todoIndex) => {
        const nextState =produce(data, draftState => {
            draftState[index].contents[todoIndex].done = !draftState[index].contents[todoIndex].done;
        })
        setData(nextState);
    }
    // 투두 삭제하기

    // 컴포넌트 렌더링
    const toDoRender = () => {
      return  data.map((item,index) =>
          <ToDoCategory key={"category"+index} >
              <ToDoCategoryButton key={"category"+index} onClick={(event)=> { toggleShowContent(event,index)}}>
                  <ToDoCategoryButtonName>{item.title}</ToDoCategoryButtonName>
                  <ToDoPlus onClick={(event ) => openModal(event,index)}>&nbsp;+&nbsp;</ToDoPlus>
              </ToDoCategoryButton>
              {item.contents.map((content, todoIndex) =>
                      item.showContent && (<ToDoThing key={"cotent"+todoIndex}>
                          <ToDoThingCheckBox onChange={(event)=> toggleDone(event,index,todoIndex)} checked={content.done}/>
                          <ToDoThingName>{content.description}</ToDoThingName>
                      </ToDoThing>)
              )}
          </ToDoCategory>
      );
    }


    return (
        <>
            <ToDoContainer bright={bright}>
                <InnerContainer  >
                    <ToDoModal exist={exist}>
                        <ToDoModalCancel onClick={openModal}>+</ToDoModalCancel>
                        <ToDoModalInput type="text" onChange={changeTodoInput} value={todoInput}/>
                        <ToDoModalButton onClick={addTodo}>+</ToDoModalButton>
                    </ToDoModal>
                    <ToDoCancel>+</ToDoCancel>
                    <ToDoTitle>
                        보관함
                    </ToDoTitle>
                    <ToDoDescription>
                        나중에 할 일을 작성할 수 있는 곳입니다.&nbsp; 향후 날짜를 설정 할 수 있습니다.
                    </ToDoDescription>
                    <ToDoCategoryContainer>
                        {toDoRender()}
                    </ToDoCategoryContainer>
                    <ToDoCategoryInputContainer>
                        <div>
                            <ToDoModalInput type="text" value={categoryInput} onChange={changeCategoryInput}/>
                            <ToDoModalButton onClick={addCategory}>+</ToDoModalButton>
                        </div>
                    </ToDoCategoryInputContainer>

                </InnerContainer>
            </ToDoContainer>
        </>
    )
}

export default ToDo;